import {Fetcher, Route, Token} from '@uniswap/sdk';
import {Configuration} from './config';
import {BigNumber, Contract, ethers, Overrides} from 'ethers';
import ERC20 from './ERC20';
import {getDefaultProvider} from '../utils/provider';
import IUniswapV2PairABI from './IUniswapV2Pair.abi.json';
import {TransactionResponse} from "@ethersproject/providers";

/**
 * An API module of BEI Cash contracts.
 * All contract-interacting domain logic should be defined in here.
 */
export class TiTiCoreContracts {
    myAccount: string;
    provider: ethers.providers.Web3Provider;
    signer?: ethers.Signer;
    config: Configuration;
    contracts: { [name: string]: Contract };
    externalTokens: { [name: string]: ERC20 };
    busdBNB: Contract;
    beiBibe: Contract;
    TITI: ERC20;
    TTG: ERC20;

    constructor(cfg: Configuration) {
        const { deployments, externalTokens } = cfg;
        const provider = getDefaultProvider();

        // loads contracts from deployments
        this.contracts = {};
        for (const [name, deployment] of Object.entries(deployments)) {
            this.contracts[name] = new Contract(deployment.address, deployment.abi, provider);
        }
        this.externalTokens = {};
        for (const [symbol, [address, decimal]] of Object.entries(externalTokens)) {
            this.externalTokens[symbol] = new ERC20(address, provider, symbol, decimal); // TODO: add decimal
        }
        this.TITI = new ERC20(deployments.TITI.address, provider, 'TITI');
        this.TTG = new ERC20(deployments.TTG.address, provider, 'TTG');

        // Uniswap V2 Pair
        this.busdBNB = new Contract(
            externalTokens['BUSD-BNB-UNI-LPv2'][0],
            IUniswapV2PairABI,
            provider,
        );

        // Uniswap V2 Pair
        this.beiBibe = new Contract(
            externalTokens['BEI-BIBE-UNI-LPv2'][0],
            IUniswapV2PairABI,
            provider,
        );

        this.config = cfg;
        this.provider = provider;
    }

    /**
     * @param provider From an unlocked wallet. (e.g. Metamask)
     * @param account An address of unlocked wallet account.
     */
    unlockWallet(provider: any, account: string) {
        const newProvider = new ethers.providers.Web3Provider(provider, this.config.chainId);

        this.signer = newProvider.getSigner(0);
        this.myAccount = account;
        for (const [name, contract] of Object.entries(this.contracts)) {
            this.contracts[name] = contract.connect(this.signer);
        }
        const tokens = [this.TITI, this.TTG, ...Object.values(this.externalTokens)];
        for (const token of tokens) {
            token.connect(this.signer);
        }
        this.busdBNB = this.busdBNB.connect(this.signer);
        console.log(` Wallet is unlocked. Welcome, ${account}!`);
    }

    get isUnlocked(): boolean {
        return !!this.myAccount;
    }

    gasOptions(gas: BigNumber): Overrides {
        const multiplied = Math.floor(gas.toNumber() * this.config.gasLimitMultiplier);
        console.log(` Gas multiplied: ${gas} -> ${multiplied}`);
        return {
            gasLimit: BigNumber.from(multiplied),
        };
    }

    async getTotalCommit(): Promise<number> {
        const genesisGroup = this.contracts['GenesisGroup'];
        const totalCommitBN = await genesisGroup.totalSupply();

        return Number(totalCommitBN / (10 ** 18));
    }

    async getUserCommit(account = this.myAccount): Promise<number> {
        const genesisGroup = this.contracts['GenesisGroup'];
        if (!account) {
            return 0;
        }
        const userCommitBN = await genesisGroup.balanceOf(account);
        return Number(userCommitBN / (10 ** 18));
    }

    async getTotalPreswap(): Promise<number> {
        const genesisGroup = this.contracts['GenesisGroup'];
        const totalPreswapBN = await genesisGroup.totalCommittedFGEN();
        return Number(totalPreswapBN / (10 ** 18));
    }

    async getUserPreswap(account = this.myAccount): Promise<number> {
        const genesisGroup = this.contracts['GenesisGroup'];
        if (!account) {
            return 0;
        }
        const userPreswapBN = await genesisGroup.committedFGEN(account);
        return Number(userPreswapBN / (10 ** 18));
    }

    async getUserBNBBalance(account = this.myAccount): Promise<number> {
        if (!account) {
            return 0;
        }
        const balance = await this.provider.getBalance(account);
        console.log(account + ':' + ethers.utils.formatEther(balance));
        return Number(ethers.utils.formatEther(balance));
    }

    async getRedeemAmount(account = this.myAccount): Promise<number[]> {
        const genesisGroup = this.contracts['GenesisGroup'];
        if (!account) {
            return [0,0];
        }
        const redeemAmountBN = await genesisGroup.getAmountsToRedeem(account);
        return [Number(redeemAmountBN[0] / (10 ** 18)), Number((redeemAmountBN[1] + redeemAmountBN[2]) / (10 ** 18))];
    }

    async getUSDCBalance(account = this.myAccount): Promise<number> {
        if (!account) {
            return 0;
        }
        const decimals = BigNumber.from(10).pow(15);
        const beiBalance = await this.externalTokens["USDC"].balanceOf(account);
        return beiBalance.div(decimals).toNumber() / 1000;
    }

    async getTITIBalance(account = this.myAccount): Promise<number> {
        if (!account) {
            return 0;
        }
        const decimals = BigNumber.from(10).pow(15);
        const beiBalance = await this.TITI.balanceOf(account);
        return beiBalance.div(decimals).toNumber() / 1000;
    }

    async getBIBEBalance(account = this.myAccount): Promise<number> {
        if (!account) {
            return 0;
        }
        const bibeBalance = await this.TTG.balanceOf(account);
        return Number(bibeBalance.toNumber() / (10 ** 18));
    }

    async getTITIUSDCReserves(): Promise<number[]> {
        const uniswapIncentive = this.contracts['TitiPairSwap'];
        const tokenReserve = await uniswapIncentive.getReserves();
        return [tokenReserve._reserve0.div(BigNumber.from(10).pow(14)) / (10000),
            tokenReserve._reserve1.div(BigNumber.from(10).pow(14)) / (10000)];
    }

    async getMMF(): Promise<number> {
        const coreController = this.contracts['CoreController'];
        const mmfConfigs = await coreController.mmfConfigs(this.contracts['MarketMakerFund'].address);
        console.log(mmfConfigs)
        return mmfConfigs.balance.div(BigNumber.from(10).pow(14)) / (10000);
    }

    async getTimeWeight(): Promise<number> {
        const uniswapIncentive = this.contracts['UniswapIncentive'];
        const timeWeight = await uniswapIncentive.getTimeWeight();
        return Number(timeWeight / (10 ** 5));
    }

    async getPegPrice(): Promise<number> {
        const uniswapIncentive = this.contracts['TitiPairSwap'];
        const tokenReserve = await uniswapIncentive.getReserves();
        return Number(tokenReserve._reserve0 / tokenReserve._reserve1);
    }

    async getTITISupply(): Promise<number> {
        const decimals = BigNumber.from(10).pow(14);
        const totoSupply = await this.TITI.totalSupply();
        return totoSupply.div(decimals).toNumber() / 10000;
    }

    async getBNBPrice(): Promise<number> {
        const tokenReserve = await this.busdBNB.getReserves();
        return Number(tokenReserve._reserve0 / tokenReserve._reserve1 * (10 ** 12));
    }

    async getLPTokenSupply(): Promise<number> {
        const lpTokenSupply = await this.beiBibe.totalSupply();
        return Number(lpTokenSupply / (10 ** 18));
    }

    async getUserLPTokenBalance(account=this.myAccount): Promise<number> {
        if (!account) {
            return 0;
        }
        const userBalance = await this.beiBibe.balanceOf(account);
        return Number(userBalance / (10 ** 18));
    }

    async getStakedBalance(account=this.myAccount): Promise<number> {
        if (!account) {
            return 0;
        }
        const stakedBalance = await this.contracts['MarketMakerFund'].balanceOf(account);
        console.log("stakedBalance", stakedBalance);
        const decimals = BigNumber.from(10).pow(14);
        return (stakedBalance.div(decimals).toNumber() / 10000);
    }

    async getRedeemableTribeRewards(account=this.myAccount): Promise<number> {
        if (!account) {
            return 0;
        }
        const earned = await this.contracts['MarketMakerFund'].earned(account);
        const rainyDayFund = await this.contracts['PAVVault'].rainyDayFund();
        const stakedBalance = await this.contracts['MarketMakerFund'].balanceOf(account);
        const totalSupply = await this.contracts['MarketMakerFund'].totalSupply();
        return Number(earned.add(rainyDayFund.mul(stakedBalance).div(totalSupply)) / (10 ** 18));
    }

    async getRewardForDuration(): Promise<number> {
        const rewardForDuration = await this.contracts['MarketMakerFund'].getRewardForDuration();
        return Number(rewardForDuration / (10 ** 18));
    }

    async getStakedLpSupply(): Promise<number> {
        const stakedLpSupply = await this.contracts['MarketMakerFund'].totalSupply();
        return Number(stakedLpSupply / (10 ** 18));
    }

    async getRewardPerToken(): Promise<number> {
        const rewardPerToken = await this.contracts['MarketMakerFund'].rewardPerToken(0);
        console.log(rewardPerToken)
        return Number(rewardPerToken / (10 ** 18));
    }

    async getStartTime(): Promise<number> {
        const starttime = await this.contracts['MarketMakerFund'].starttime();
        return starttime.toNumber();
    }

    async getRainyDayFund(): Promise<number> {
        const rainyDayFund = await this.contracts['PAVVault'].rainyDayFund();
        return rainyDayFund.div(BigNumber.from(10).pow(14)).toNumber() / (10000);
    }

    async getTitiCirculation(): Promise<number> {
        const totalSupply = await this.TITI.totalSupply();
        const uniswapIncentive = this.contracts['TitiPairSwap'];
        const tokenReserve = await uniswapIncentive.getReserves();
        const marketMakerFund = this.contracts['MarketMakerFund'];
        const totalReward = await marketMakerFund.totalReward();
        return totalSupply.sub(tokenReserve._reserve1).sub(totalReward).div(BigNumber.from(10).pow(14)).toNumber() / (10000) - 100;
    }

    async getPRV(): Promise<number> {
        const uniswapIncentive = this.contracts['TitiPairSwap'];
        const tokenReserve = await uniswapIncentive.getReserves();
        const coreController = this.contracts['CoreController'];
        const mmfConfigs = await coreController.mmfConfigs(this.contracts['MarketMakerFund'].address);
        //const rainyDayFund = await this.contracts['PAVVault'].rainyDayFund();

        return tokenReserve._reserve0.sub(mmfConfigs.balance).div(BigNumber.from(10).pow(14)).toNumber() / (10000);
    }

    async getFeiTribeReserves(): Promise<number[]> {
        const reserves = await this.contracts['IDO'].getReserves();
        return [Number(reserves.feiReserves / (10 ** 18)), Number(reserves.tokenReserves / (10 ** 18))];
    }

    async getTokenPriceFromUniswap(tokenContract: ERC20): Promise<string> {
        await this.provider.ready;

        const { chainId } = this.config;
        const { DAI } = this.config.externalTokens;

        const dai = new Token(chainId, DAI[0], 18);
        const token = new Token(chainId, tokenContract.address, 18);

        try {
            const daiToToken = await Fetcher.fetchPairData(dai, token, this.provider);
            const priceInDAI = new Route([daiToToken], token);
            return priceInDAI.midPrice.toSignificant(3);
        } catch (err) {
            console.error(`Failed to fetch token price of ${tokenContract.symbol}: ${err}`);
        }
    }

    async buy(amount: BigNumber): Promise<TransactionResponse> {
        const coreController = this.contracts["CoreController"];
        const gas = await coreController.estimateGas.buyTITI(amount);
        return await coreController.buyTITI(amount, this.gasOptions(gas));
    }

    async sell(amount: BigNumber): Promise<TransactionResponse> {
        const coreController = this.contracts["CoreController"];
        const gas = await coreController.estimateGas.sellTITI(amount);
        return await coreController.sellTITI(amount, this.gasOptions(gas));
    }

    async stake(amount: BigNumber): Promise<TransactionResponse> {
        const marketMakerFund = this.contracts["MarketMakerFund"];
        const gas = await marketMakerFund.estimateGas.stake(amount);
        return await marketMakerFund.stake(amount, this.gasOptions(gas));
    }

    async exit(): Promise<TransactionResponse> {
        const marketMakerFund = this.contracts["MarketMakerFund"];
        const gas = await marketMakerFund.estimateGas.exit();
        return await marketMakerFund.exit(this.gasOptions(gas));
    }

    async claim(): Promise<TransactionResponse> {
        const marketMakerFund = this.contracts["MarketMakerFund"];
        const gas = await marketMakerFund.estimateGas.getReward();
        return await marketMakerFund.getReward(this.gasOptions(gas));
    }

    async reorders(): Promise<TransactionResponse> {
        const coreController = this.contracts["CoreController"];
        const gas = await coreController.estimateGas.reorders();
        return await coreController.reorders(this.gasOptions(gas));
    }
}
