import React from 'react'
import { Provider } from 'react-redux'
import {
  BrowserRouter as Router,
  Redirect,
  Route,
  Switch,
} from 'react-router-dom'
import './App.css'
import { ThemeProvider } from 'styled-components'
import { UseWalletProvider } from 'use-wallet'
import Updaters from './state/Updaters'
import TiTiProvider from './contexts/TiTiProvider'
import ModalsProvider from './contexts/Modals'
import Popups from './components/Popups'
import store from './state'
import theme from './theme'
import config from './config'
import Loadable from './components/Loadable'
// import Home from "./views/Home";
// import Dashboard from "./views/Dashboard";
// import Exchange from "./views/Exchange";
// import LPPools from "./views/LPPools";
// import ReOrders from "./views/ReOrders";

const Home = Loadable(() => import('./views/Home'))
// const Dashboard = Loadable(() => import('./views/Dashboard'))
// const Exchange = Loadable(() => import('./views/Exchange'))
// const LPPools = Loadable(() => import('./views/LPPools'))
// const ReOrders = Loadable(() => import('./views/ReOrders'))

const App: React.FC = () => {
  return (
    <Providers>
      <Router>
        <Switch>
          <Route path="/" exact>
            <Home />
          </Route>
          {/* <Route path="/mamms" exact>
                        <Exchange />
                    </Route>
                    <Route path="/mmfs">
                        <LPPools />
                    </Route>
                    <Route path="/reorders" exact>
                        <ReOrders />
                    </Route>
                    <Route path="/dashboard" exact>
                        <Dashboard />
                    </Route> */}
          <Redirect to="/"></Redirect>
        </Switch>
      </Router>
    </Providers>
  )
}

const Providers: React.FC = ({ children }) => {
  return (
    <ThemeProvider theme={theme}>
      <UseWalletProvider chainId={config.chainId}>
        <Provider store={store}>
          <Updaters />
          <TiTiProvider>
            <ModalsProvider>
              <Popups />
              {children}
            </ModalsProvider>
          </TiTiProvider>
        </Provider>
      </UseWalletProvider>
    </ThemeProvider>
  )
}

export default App
