import React, { createContext, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';
import BEICash from '../../titi-core-contracts';
import config from '../../config';

export interface BEICashContext {
    beiCash?: BEICash;
}

export const Context = createContext<BEICashContext>({ beiCash: null });

export const TiTiProvider: React.FC = ({ children }) => {
    const { ethereum, account } = useWallet();
    const [beiCash, setBEICash] = useState<BEICash>();

    useEffect(() => {
        if (!beiCash) {
            const newBeiCash = new BEICash(config);
            if (account) {
                // wallet was unlocked at initialization
                newBeiCash.unlockWallet(ethereum, account);
            }
            setBEICash(newBeiCash);
        } else if (account) {
            beiCash.unlockWallet(ethereum, account);
        }
    }, [ethereum, beiCash, account]);

    return <Context.Provider value={{ beiCash }}>{children}</Context.Provider>;
};
